.task-container {
    display: grid;
    grid-template-rows: 1fr 5fr 1fr;

    grid-row-gap: 30px;

    row-gap: 30px;
    /* flex-direction: column; */

    max-width: 470px;
    width: 100%;
    height: 521px;

    padding: 20px;
    margin-bottom: 30px;

    background: linear-gradient(
        295.61deg,
        rgba(160, 160, 160, 0.308) -0.03%,
        rgba(80, 80, 80, 0.48) 100%
    );
    mix-blend-mode: normal;
    box-shadow: 1px 2px 10px 4px rgba(0, 0, 0, 0.178);
    -webkit-backdrop-filter: blur(9.51399px);
            backdrop-filter: blur(9.51399px);

    border-radius: 20px;
}

.task-container a {
    color: white;
}

.task-container .title {
    font-size: 40px;
    text-align: center;
}

.task-container .description {
    font-size: 20px;
    height: -webkit-max-content;
    height: max-content;
}

.task-container .cost {
    font-size: 20px;
    align-self: center;
}

.task-container .file {
    height: -webkit-max-content;
    height: max-content;
}

.task-container input {
    color: white;

    height: 45px;
    /* margin-left: 20px; */
    font-size: 25px;
    width: 100%;
    /* max-width: 350px; */
    border: none;
    padding: 0 10px;
    border-radius: 12px;

    background: #ffffff3e;
    mix-blend-mode: multiply;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    transition: all 0.2s;
}

.task-container input::-webkit-input-placeholder {
    color: rgb(192, 192, 192);
}

.task-container input:hover {
    background: #ffffffac;
    mix-blend-mode: soft-light;
    color: black;
    border: none;
}

.task-container input:focus {
    background: white;
    mix-blend-mode: soft-light;
    color: black;
}

.task-container input:hover::-webkit-input-placeholder { 
    color: rgb(103, 103, 103);
}

.task-container input:focus::-webkit-input-placeholder {
    color: rgb(70, 70, 70);
}

.cardRotate-anim {
    -webkit-animation: cardRotate .2s normal ease-in-out;
            animation: cardRotate .2s normal ease-in-out;
}

@-webkit-keyframes cardRotate {
    from {
        -webkit-transform: rotate3d(0, 1, 0, 120deg);
                transform: rotate3d(0, 1, 0, 120deg);
    }
    to {
        -webkit-transform: rotate3d(0, 1, 0, 0deg);
                transform: rotate3d(0, 1, 0, 0deg);
    }
}

@keyframes cardRotate {
    from {
        -webkit-transform: rotate3d(0, 1, 0, 120deg);
                transform: rotate3d(0, 1, 0, 120deg);
    }
    to {
        -webkit-transform: rotate3d(0, 1, 0, 0deg);
                transform: rotate3d(0, 1, 0, 0deg);
    }
}
