.signin-container u .need-help {
    position: absolute;
    width: 100%;
    height: 60px;
    background: white;
    color: black;
    top: -70px;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 10px;

    -webkit-animation: ease-in-out .3s forwards helpAnim;

            animation: ease-in-out .3s forwards helpAnim;
}

.signin-container u .need-help::after {
    position: absolute;
    content: "";
    width: 0px;
    height: 0px;
    background: transparent;
    border-style: solid;
    border-width: 10px 5px 0 5px;
    border-color: white transparent transparent transparent;
    bottom: -9px;
}

