@-webkit-keyframes helpAnim {
    0% {
        width: 100%;
        height: 0;
        color: transparent;
        top: 0;
    }
    90% {
        width: 100%;
        height: 60px;
    }
    100% {
        width: 100%;
        height: 60px;
        color: black;
    }
}

@keyframes helpAnim {
    0% {
        width: 100%;
        height: 0;
        color: transparent;
        top: 0;
    }
    90% {
        width: 100%;
        height: 60px;
    }
    100% {
        width: 100%;
        height: 60px;
        color: black;
    }
}

